/**
 * popperpopperdropcascader
 **/
import { findComponentDownward } from '../utils'
export default {
    computed: {
        popperComponent () {
            return findComponentDownward(this, 'zov-popper')
        }
    },
    methods: {
        dropUpdate () {
            this.popperComponent.popper.update()
        }
    }
}
